<template>
	<view>
		
		<!-- 顶部选项卡 -->
	<swiperTabHead :tabBars="tabBars" :tabIndex="tabIndex" @tabtap="tabtap"></swiperTabHead>
		
	<view class="uni-tab-bar">
	            <swiper class="swiper-box" :style="{height:swiperheight+'px'}" :current="tabIndex" @change="tabChange">
	                <swiper-item v-for="(items,index) in newlist" :key="index">
	                    <scroll-view scroll-y class="list" @scrolltolower="loadmore(index)">
						  <template v-if="items.list.length>0">
							<!-- 列表 -->
	                       <block v-for="(item2,index2) in items.list" :key="index2">
							   <!-- 列表样式 -->
							<topic-list :item="item2" :index="index2"></topic-list>
	                     
	                       </block> 
						   </template>
						   <template v-else>
							  <no-thing></no-thing>
						   </template>
						   <!-- 上拉加载 -->
						   <load-more :loadmore="items.loadmore"></load-more>
	                    </scroll-view>
	                </swiper-item>
	            </swiper>
				
	 </view>
			
	 
		
	</view>
</template>

<script>
	const demo = [
		{
			cover:"/static/demo/user2-160x160.jpg",
			title:"#话题名称#",
			desc:"#话题描述#",
			today_count:0,
			news_count:10
		},
		{
			cover:"/static/demo/user2-160x160.jpg",
			title:"#话题名称#",
			desc:"#话题描述#",
			today_count:0,
			news_count:10
		},
		{
			cover:"/static/demo/user2-160x160.jpg",
			title:"#话题名称#",
			desc:"#话题描述#",
			today_count:0,
			news_count:10
		},
		{
			cover:"/static/demo/user2-160x160.jpg",
			title:"#话题名称#",
			desc:"#话题描述#",
			today_count:0,
			news_count:10
		}
	];
	
	// 引入模板
	import topicList from '@/components/news/topic-list.vue'
	import swiperTabHead from "@/components/common/swiper-tab-head.vue";
	import loadMore from '@/components/common/load-more.vue';
	export default {
		components:{
			topicList,
			swiperTabHead,
			loadMore
		},
		data() {
			return {
				//顶部选项卡
				tabIndex:0,
				tabBars:[
				      { name:"关注",id:"guanzhu" },
					  { name:"推荐",id:"tuijian" },
					  { name:"体育",id:"tiyu" },
					  { name:"热点",id:"redian" },
					  { name:"财经",id:"caijing" },
					  { name:"娱乐",id:"yule"   },
				],
				swiperheight:0,//高度
				newlist:[]
				
			}
		},
		//监听点击导航栏搜索框
		onNavigationBarSearchInputClicked() {
			uni.navigateTo({
				url: '../search/search'
			})
		},
		// 监听导航按钮点击事件
		onNavigationBarButtonTap() {
			uni.navigateTo({
				url: '../add-input/add-input'
			})
		},
		onLoad() {
			uni.getSystemInfo({
				success:(res)=>{
					let height = res.windowHeight-uni.upx2px(100)
					this.swiperheight = height;
				}
			    })
				//根据选项生成列表
				this.getData();
		},
		
		methods: {
			//获取数据
			getData(){
				var arr = [];
				for (let i = 0; i < this.tabBars.length; i++) {
					let obj = {
						loadmore:"",
						list:[]
					}
					if(i<3) {
						obj.list = demo;
						obj.loadmore = '上拉加载更多';
					}
					arr.push(obj);
				}
				this.newlist = arr;
			},
			//点击切换导航
			tabtap(index){
				this.tabIndex = index;
				
			},
			tabChange(e){
			   this.tabIndex = e.detail.current;
			},
			// 关注
			follow(e){
				// e是索引
					this.list[e].isFollow = true;
					uni.showToast({
						title: '关注成功'
					});
			},
			//顶踩操作
			doSupport(e){
				//拿到当前对象
				let item = this.list[e.index];
				let msg = e.type === 'support' ? '顶' : '踩';
				
				//判断之前是否已经顶踩
				if(item.support.type === ''){
					item.support[e.type+'_count']++;
				} else if(item.support.type === 'support' && e.type === 'unsupport'){
					//顶 -1
					item.support.support_count--;
					//踩 +1
					item.support.unsupport_count++;
				} else if(item.support.type === 'unsupport' && e.type === 'support'){
					//之前踩了
					// 顶+1
					item.support.support_count++;
					// 踩-1
					item.support.unsupport_count--;
				}
				
				item.support.type = e.type;
				uni.showToast({title: msg + '成功'})	;
			},
			// 上拉加载更多
			loadmore(index){
				//拿到当前列表
				let item = this.newlist[index];
				// 判断是否处于可加载状态
				if(item.loadmore !== '上拉加载更多') return;
				
				item.loadmore = '加载中...';
				//模拟数据请求
				setTimeout(()=>{
					// 加载数据
					item.list = [...item.list,...item.list];
					item.loadmore = '上拉加载更多';
				},2000);
			}
		}
	}
</script>

